import {
  Dialog,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
} from "@mui/material";

export default function ConfirmModal({
  handleClearHistory,
  setOpenConfirm,
  openConfirm,
}) {
  return (
    <Dialog open={openConfirm} onClose={() => setOpenConfirm(false)}>
      <DialogContent>
        <DialogContentText sx={{ color: "black" }}>
                ?
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button autoFocus onClick={() => setOpenConfirm(false)}>
          
        </Button>
        <Button onClick={handleClearHistory} autoFocus>
          
        </Button>
      </DialogActions>
    </Dialog>
  );
}
